.new-blog-nav {
  display: flex;
  align-items: flex-start;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: auto;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-top: 30px;
  padding-bottom: 20px;
  padding-right: 35px;
  padding-left: 20px; }
  .new-blog-nav .left-block {
    margin-right: auto; }
    .new-blog-nav .left-block .brand {
      display: block;
      width: 164px;
      height: 50px;
      background-image: url("../img/logo.svg");
      background-position: 50% 50%;
      background-size: 158px;
      background-repeat: no-repeat; }
  .new-blog-nav .right-block {
